@import url(https://fonts.googleapis.com/css?family=Roboto:400,700,900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}body{margin:20px 0;padding:0;line-height:1;font-family:"Roboto", sans-serif;color:#202020;background-color:#fbfbfb;font-smooth:always;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}main{display:flex;max-width:1200px;margin:auto;justify-content:center}.mr{margin-right:20px;background-color:green}

